@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@400;800&display=swap);
.App {
  text-align: center;
  font-family: 'Orbitron', 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



.disabled {
    pointer-events: none;
    opacity: 0.3;
  }

.Calculator{
    display: grid;
    grid-template-columns: repeat(5, 1fr);

    --color1: #01260A;
    --color2: #14732B;
    --color3: #2ABF40;
    --color4: #44F24F;
    --color5: #0D0D0D;
    padding: 1em;
    border: 10px;

    outline:0;
}

.Title {
    grid-area: 1/1/2/6;
    padding: 1em;
    font-size: x-large;
}

.Display {
    grid-area: 2/1/3/6;
    background-color: var(--color1);
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    background-color: var(--color5);

}

.Operation {
    grid-area: 1/1/1/6;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 0.1em;
    overflow: hidden
}

.Operation > span{
    grid-area: 1fr;
}

.ShowDisplay{
    grid-area: 2/1/2/6;
    padding: 0.5em;
}

.Reset {
    color: var(--color2);
    text-align: left;
    padding-left: 0.7em;
    padding-right: 0.2em;
}

.EqualDisplay {
    grid-area: 1/6/3/7;

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    -webkit-align-items: center;
            align-items: center;
}

.UndoRedo {
    font-size: small;
    padding: 0.4em;
}
.Equal {
    grid-area: 2/1/3/3;
    padding: 0.7em;
}

.Buttons{
    grid-area: 3/1/6/6;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}

.CalculatorButton {
    border-radius: 5px;
}

.CalculatorButton:hover{
    color: var(--color4);
    background-color: var(--color2);
    box-shadow: 0px 2px 5px var(--color3);
    cursor: pointer;
}
.CalculatorButton:disabled{
    color: red;
}




.Numbers {
    grid-area: 1/1/5/4;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-content: stretch;
            align-content: stretch;
}

.Number {
    padding: 1em;
}
.Operators {
    grid-area: 1/4/5/5;
    display: grid;
}

.BasicMathOperators {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    padding: 0px;
}

.Operator {
    grid-area: 1fr;
    padding: 0.5em;
}


